@import url(https://fonts.googleapis.com/css2?family=PT+Sans&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'PT Sans', sans-serif;
}

body
{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  background: #252424;
}

h1, h2
{
  color: white;
  text-align: center;
}

.box
{
    padding: 5rem 5rem;
    margin-top: 10%;
    border-radius: 10px;
    background-color: aqua;
}
.search
{
    padding: 0 20px;
}
.inputFeild
{
    width: 10rem;
    height: 2rem;
    border-radius: 10px;
}
.info 
{
    margin-top: 1rem;
    position: relative;
    bottom: 0;
    width: 100%;
    height: 50%;
    z-index: 4;
    line-height: 2.5rem;
    text-align: center;
}
.fas {
    font-size: 2rem;
    color: rgb(53, 9, 94);
}
.temp
{
    color: black;
}
h4{
    color: grey;
}
